import React from "react";
import EmailIcon from "@material-ui/icons/Email";
import GithubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import "../styles/Home.css";

function Home() {
  return (
    <div className="home">
      <div className="about">
        <h2>Hi, My Name is Munashe Moyo</h2>
        <div className="prompt">
          <p>
            A junior software developer with a passion for learning and
            creating.
          </p>
          <LinkedInIcon />
          <EmailIcon />
          <GithubIcon />
        </div>
      </div>
      <div className="skills">
        <h1>Skills</h1>
        <ol className="list">
          <li className="item">
            <h2>Front-End</h2>
            <span>
              ReactJs, HTML, CSS, Javascript,React Native, BootStrap, Npm
            </span>
          </li>
          <li className="item">
            <h2>Back-End</h2>
            <span> NodeJs, PHP, MongoDB,MySQL, Python, .NET</span>
          </li>
          <li className="item">
            <h2>Languges</h2>
            <span>Javascript, PHP, Python, C#, C, Java</span>
          </li>
        </ol>
      </div>
    </div>
  );
}

export default Home;
